import kpsImageCutter from "@/components/ksp-image-cutter/ksp-image-cutter.vue";
import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	components: {kpsImageCutter},
	data() {
		return {
		  picshow:true,
		  url: "",
		  path: "",
		  imgpath:'',
		  finish:false,
		  fileList:[], //图片本地地址列表
		  imgArr:[], //上传阿里后的列表地址
		  content: '',
		  name: '',
		  mobile: '',
		  current_price: '',
		  original_price: '',
		  address: '',  //绑定地址
		 
		  category_id: '',
		  category_name: '',
		  showList :false,
		  // 分类列表
		  list1:[],
		  list: []
		}
	},
	watch: {
	    finish () {
		  
	      if (this.finish) {
			this.imgArr = JSON.stringify(this.imgArr)
	        uni.hideLoading();
	        this.$api.SmallStretchCreate({
	          token: uni.getStorageSync("token"),
	          user_id: uni.getStorageSync("userId"),
	          name: this.name,
	          original_price: this.original_price,
	          current_price: this.current_price,
	          mobile: this.mobile,
	          address: this.address,
	          category_id: this.category_id,
	          content: this.content,
	          images: this.imgArr,
			  image:this.imgpath
	        }).then(res => {
			  Toast(res.data.msg)
	          // this.$toast(res.msg)
	          if (res.data.code === '200') {
				  uni.reLaunch({
				  	url:'../SmallStretchStatus/SmallStretchStatus'
				  })
				  // uni.navigateTo({
				  // 	url:'../SmallStretchStatus/SmallStretchStatus'
				  // })
	          }
	        })
	      }
	    }
	},
	mounted() {
	  	
	},
	onLoad() {
	  	this.getClassify()
	},
	methods: {
		// 选择商品种类
		pickershop(e){
			// console.log(e.detail.value)
			this.category_id = this.list[e.detail.value].id
			this.category_name = this.list[e.detail.value].name
		},
		// 商品分类
		getClassify () {
		      this.$api.SmallStretchGoodsCategory({
		      }).then(res => {
		        this.list = res.data.data
				res.data.data.forEach((item, index) => {
				     this.list1.push(item.name)
				  })
		      })
		},
		// 选择城市
		pickercity(e){
			this.address = e.detail.value[0].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔/g, '') + ' ' +e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
		},
		// 原价匹配数字。
		oldprice(){
			var re=/^[0-9]\d*$/;
			if(!re.test(this.original_price)){
				this.original_price = ''
				Toast('只允许输入正整数')	
			}
		},
		nowprice(){
			var re=/^[0-9]\d*$/;
			if(!re.test(this.current_price)){
				this.current_price = ''
				Toast('只允许输入正整数')	
			}
		},
		inputmobile(){
			  var pattern = /^[\d-]+$/
			  if (!pattern.test(this.mobile)) {
				this.mobile = ''
				Toast('电话号只允许数字和-组成')
			  }
		},
		// 主图下面的图片
		sendTwo(){
			var that = this
			async function put () {
			  for (let i = 0; i < that.fileList.length; i++) { 
					  await  uni.uploadFile({
								  url: app.aliUrl,
								  filePath: that.fileList[i].url,
								  name: 'file',
								  formData: {
								    key:"wx/"+"${filename}",
								    signature: app.signature,
								    policy:app.policy,
								    OSSAccessKeyId:app.OSSAccessKeyId,
								    success_action_status:'201'
								  }
						 }).then(res=>{
							 that.imgArr.push({
								 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0]
							 })
							 if(that.fileList.length === that.imgArr.length){
								 that.finish = true
							 }
							 
						 })
		          }
			}
			 put()
		},
		// 发布
		send () {
			  var that = this
		      var pattern = /^[\d-]+$/			      
		      if (this.name && this.mobile && this.original_price && this.current_price && this.address && this.category_id && this.fileList.length > 0 && this.content && (Number(this.current_price) <= Number(this.original_price)) && this.path) {
		        Dialog.confirm({
		          title: '发布提示',
		          message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担。平台审核中发现问题会立即下架您所发布的内容，确认发布？'
		        }).then(() => {
		          uni.showLoading({
		              title: '加载中'
		          });
				  uni.uploadFile({
						  url: app.aliUrl,
						  filePath: that.path,
						  name: 'file',
						  formData: {
						    key:"wx/"+"${filename}",
						    signature: app.signature,
						    policy:app.policy,
						    OSSAccessKeyId:app.OSSAccessKeyId,
						    success_action_status:'201'
						  }
				 }).then(res=>{
					 that.imgpath = res[1].data.split('<Location>')[1].split("</Location>")[0]
					 setTimeout(()=>{
						 that.sendTwo()
					 }, 500);
					 
				 })
				  
		        }).catch(() => {
		
		        })
		      } else {
		        if (!this.name) {
		          Toast('商品名称没有填写')
		          return
		        }
				if (!this.original_price) {
				  Toast('商品原价没有填写')
				  return
				}
				if (!this.current_price) {
				  Toast('商品现价没有填写')
				  return
				}
		        if (!this.mobile) {
		          Toast('客服电话没有填写')
		          return
		        }
				if (!pattern.test(this.mobile)) {
				  Toast('电话号只允许数字和-组成')
				  return
				}
		        
		        if (!this.address) {
		          Toast('所在城市没有填写')
		          return
		        }
		        if (!this.category_id) {
		          Toast('商品类别没有填写')
		          return
		        }
				if (!this.content) {
				  Toast('商品详情没有填写')
				  return
				}
				if (!this.path) {
				  Toast('请选择一张主图')
				  return
				}
		        if (this.fileList.length <= 0) {
		          Toast('介绍图片不能为空')
		          return
		        }
		        if (Number(this.current_price) > Number(this.original_price)) {
		          Toast('商品现价要比原价底！')
		        }
		      }
		    },
		// 删除图片
		del(e){
			this.fileList = this.fileList.filter((item,index)=> index!=e.detail.index)
		},
		// 图片上传后的预览
		afterRead(event) {
		      const { file } = event.detail;
		      // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
		      this.fileList.push({url:file[0].path})
		},
		// 这个是选择主图裁剪的
		chooseImage() {
			uni.chooseImage({
				success: (res) => {
					uni.pageScrollTo({
						scrollTop:0
					})
					// 设置url的值，显示控件
				    this.url = res.tempFilePaths[0];
					this.picshow = false
				}
			});
		},
		onok(ev) {
			this.path = ev.path;
			this.url = "";
			this.picshow = true
			setTimeout(function() {
				uni.pageScrollTo({
					scrollTop:600
				})
			}, 200);
		},
		oncancle() {
			// url设置为空，隐藏控件
			this.url = "";
			this.picshow = true
			setTimeout(function() {
				uni.pageScrollTo({
					scrollTop:600
				})
			}, 200);
		}
	}
}
